/* styles.css */
.button-custom {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin: 10px;
  }
  
  .button-custom:hover {
    background-color: #0056b3;
  }
  
  .button-custom:disabled {
    background-color: gray;
    cursor: not-allowed;
  }
  
  .login-container {
    text-align: center;
    margin-top: 50px;
    color: aliceblue;
  }
  
  .error {
    color: red;
  }
  